*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  color: $base-black;
  font-family: $BASE_FONT_FAMILY;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -webkit-overflow-scrolling: touch;
  text-size-adjust: 100%;
  width: 100%;

  @include pc {
    min-width: $PC_CONTENT_WIDTH;
  }
}

a {
  color: inherit;
  font-size: inherit;
  text-decoration: none;
}

img,
svg {
  height: auto;
  max-width: 100%;
}

button,
input[type="submit"] {
  appearance: none;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
}

ul,
ol,
li {
  list-style-type: none;
}

em,
address {
  font-style: normal;
}

a[href^="tel:"] {
  @include pc {
    cursor: default;
  }
  @include sp {
    text-decoration: underline;
  }
}
